<template>
  <div class="progressTest">
    <div class="title">
      <h3>快速上手</h3>
      <p>全部引入</p>
      <h5 class="tit3">doll-ui 支持完整引入或者根据需要引入部分组件。 我们先介绍如何完整引入 doll-ui，后介绍部分引入 doll-ui。</h5>
      <h6 class="daima">
        <p>import DollUi from "dollui"</p>
        <p>Vue.use(DollUi)</p>
      </h6>
      <p>按需导入</p>
      <h5 class="tit3">接下来，如果你只希望引入部分组件，比如 Button 和 Select，那么需要在 main.js 中写入以下内容</h5>
      <h6 class="daima">
        <p>import Vue from 'vue';</p>
        <p>import { Button, Select } from 'element-ui';</p>
        <p> import App from './App.vue';</p>
        <p>Vue.component(Button.name, Button);</p>
        <p>Vue.component(Select.name, Select);</p>
      </h6>
    </div>
  </div>
</template>

<script>
  export default {
    name: "install"
  }
</script>

<style lang="scss" scoped>
  .bottom {
    margin-bottom: 50px;
  }

  .progressTest {
    margin-top: 50px;
  }

  .title > h3 {
    font-size: 28px;
    font-weight: 400;
    color: #1f2f3d;
    margin-bottom: 15px;
  }

  .title > p {
    font-size: 18px;
    color: #5e6d82;
    line-height: 30px;
  }

  .tit3 {
    font-size: 14px;
    line-height: 40px;
  }

  .daima {
    background-color: #fafafa;
    border: 1px solid #eaeefb;
    font-size: 14px;
    padding: 18px 24px;
    line-height: 1.8;
    margin-bottom: 25px;
    border-radius: 4px;
    font-smoothing: auto;
    font-family: Menlo, Mona  co, Consolas, Courier, monospace;

    p {
      color: #000;
    }
  }
</style>



