import Vue from 'vue'
import Router from 'vue-router'
import home from  "../pages/home/home"
import codes from "../pages/codes/codes"
import gallery from "../pages/gallery/gallery"
// import HomeSwiper from "@/pages/gallery/components/Swiper"
//pc
import Pc from '@/pages/gallery/components/pc'
//m
import Mob from '@/pages/gallery/components/m'
//安装
import install from  "../components/install"
//使用
import use from "../components/use"
//贡献
import gongxian from "../components/gongxian"
//更新
import updata from  "../components/updata"

import about from "../pages/about/about"
//单选按钮
import radio from "../components/radio/orderHome"
// 下拉框
import select from "../components/select/DlSelectFather"
//表单
import from from "../components/form"
//导航条
import dlNav from "../components/dlNav"
//登录验证
import loginCheck from "../components/loginCheck"
//分页
import paging from "../components/paging"
//面包屑
import breadCurmbs from "../components/breadCurmbs"
//评分
import Rate from "../components/Rate"
//手风琴
import accordion from "../components/accordion"
//验证码
import verificationCode from "../components/verificationCode"
//折叠
import fold from "../components/fold"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:"/home"
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    // 组件库
    {
      path: '/codes',
      name: 'codes',
      component: codes,
      children:[
        {
          path:"",
          redirect: "install"
        },
        {
          path:"install",
          name:"install",
          component:install
        },{
          path:"use",
          name:"use",
          component:use
        },{
          path:"updata",
          name:"updata",
          component:updata
        },
        {
          path:"gongxian",
          name:"gongxian",
          component:gongxian
        },
        {
        path:"radio",
        name:"radio",
        component:radio
      },{
        path:"select",
        name:"select",
        component:select
      },
        {
          path:"from",
          name:"from",
          component:from
        },{
          path:"nav",
          name:"nav",
          component:dlNav
        },{
          path:"loginCheck",
          name:"loginCheck",
          component:loginCheck
        },{
          path:"paging",
          name:"paging",
          component:paging
        },{
          path:"breadCurmbs",
          name:"breadCurmbs",
          component:breadCurmbs
        },{
          path:"Rate",
          name:"Rate",
          component:Rate
        },{
          path:"accordion",
          name:"accordion",
          component:accordion
        },
        {
          path: "verificationCode",
          name: "verificationCode",
          component: verificationCode
        }, {
          path: "fold",
          name: "fold",
          component: fold
        },

      ]
    },
    //关于我们
    {
      path:'/about',
      name:'about',
      component:about
    },
    {
      path:'/gallery',
      name:'gallery',
      component:gallery,
      children:[
        {
          path:'pc',
          name:'pc',
          component:Pc
        },
        {
          path:'m',
          name:'Mob',
          component:Mob
        }
      ]
    }
    // ,{
    //   path:'/swiper',
    //   name:'HomeSwiper',
    //   component:HomeSwiper
    // }
  ]
})



