<template>
    <div class="progressTest">
        <div class="title">
            <h3>Paging</h3>
            <p>常用的分页组件。</p>
        </div>
        <div class="usage">
            <h3 class="title">基础用法</h3>
            <p>分页组件的基础用法。</p>
            <div class="useProgress">
                <div class="bottom">
                    <dl-paging :totalPage="totalPage" :currentPage="currentPage"></dl-paging>
                </div>
            </div>
            <div class="content">
                <div class="description">你可以使用<span>currentPage</span>属性来定义起始页，用<span>totalPage</span>属性来定义最后一页</div>
                <div class="code">
                    <p><span><</span>dl-paging :totalPage="totalPage" :currentPage="currentPage"<span>></span><span><</span>/dl-paging<span>></span></p>
                    <p class="top"><span><</span>script<span>></span></p>
                    <p>&nbsp;&nbsp;export default {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;data() {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;currentPage:"1",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;totalPage:"100"</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;}</p>
                    <p><span><</span>/script<span>></span></p>
                </div>
            </div>
            <div class="show" @click="show">查看代码</div>
        </div>
        <div class="usage">
            <h3 class="title">Attributes</h3>
            <div class="attributesTable">
                <table>
                    <thead>
                        <tr>
                            <td>参数</td>
                            <td>说明</td>
                            <td>类型</td>
                            <td>可选值</td>
                            <td>默认值</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>currentPage</td>
                            <td>起始页</td>
                            <td>String</td>
                            <td>—</td>
                            <td>—</td>
                        </tr>
                        <tr>
                            <td>totalPage</td>
                            <td>最后一页</td>
                            <td>String</td>
                            <td>—</td>
                            <td>—</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    export default {
        name: "paging",
        data(){
            return{
                currentPage:"1",
                totalPage:"100"
            }
        },
        methods:{
            show(){
                $(".content").toggle();
            }
        }
    }
</script>

<style scoped>
    .bottom{
        margin-bottom: 50px;
    }
    .progressTest{
        margin-top: 50px;
    }
    .title>h3{
        font-size: 28px;
        font-weight: 400;
        color: #1f2f3d;
        margin-bottom: 15px;
    }
    .title>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .usage>h3{
        font-size: 22px;
        font-weight: 400;
        color: #1f2f3d;
        margin-top: 55px;
        margin-bottom: 15px;
    }
    .usage>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .useProgress{
        border: 1px solid #ebebeb;
        padding: 24px;
    }
    .content{
        background-color: #fafafa;
        padding: 10px;
        border: 1px solid #ebebeb;
        border-top: none;
        display: none;
    }
    .top{
        margin-top: 10px;
    }
    .description{
        border: 1px solid #ebebeb;
        background-color: #fff;
        line-height: 22px;
        padding: 20px;
        border-radius: 3px;
        font-size: 14px;
        color: #666;
    }
    .description>span{
        color: #5e6d82;
        background-color: #e6effb;
        margin: 0 4px;
        display: inline-block;
        padding: 1px 5px;
        font-size: 12px;
        border-radius: 3px;
        height: 18px;
        line-height: 18px;
    }
    .code{
        padding: 20px 10px 10px;
        color: #3182bd;
        font-size: 12px;
    }
    .code>p{
        line-height: 20px;
    }
    .show{
        border: 1px solid #ebebeb;
        border-top: none;
        font-size: 14px;
        line-height: 44px;
        text-align: center;
        color: #5e6d82;
    }
    table{
        width: 100%;
        font-size: 14px;
        margin-bottom: 30px;
    }
    table tr{
        border-bottom: 1px solid #ebebeb;
    }
    table td{
        padding: 15px;
        color: #606266;
    }
    thead td{
        color: #909399;
    }
</style>


