<template>
    <div>
      <dl-header :active="titleActive"></dl-header>
      <!-- banner -->

      <!-- //banner -->

      <div class="sec">
        <!--tabs-->
        <div class="codes" >
          <div class="codeLeft">
            <ul v-for="(i,index) in compon" :key="index">
              <li class="code-title">{{i.name}}</li>
              <router-link v-for="(j,ind) in i.children" :key="ind" tag="li" :to="j.url"  class="list" >{{j.name}}</router-link>
            </ul>
          </div>
          <div class="codeRight">
            <router-view></router-view>
          </div>
        </div>
        <!--/tabs-->

        <!-- //gallery -->
      </div>
    </div>
</template>

<script>
  import dlHeader from "@/components/dl-header"
  import dlFooter from "@/components/dl-footer"
    export default {
        name: "gallery",
      data() {
        return {
          titleActive:0,
          compon: [
            {
              name:"案例展示",
              children:[
                {
                url: "/gallery/pc",
                name: "-PC端"
              },
                {
                  url: "/gallery/m",
                  name: "-移动端"
                },
                {
                  url:'/codes/star',
                  name:"-响应式"
                },
                {
                  url:'/codes/page',
                  name:"-大数据"
                },
                {
                  url:'/codes/page',
                  name:"-微信小程序"
                }
              ]
            }

          ]

        }
      },
      components:{
        dlHeader,
        dlFooter
      },
      methods:{
        // show(){
        //   console.log(this.tag)
        //   this.tag = this.tag === false ? true : false
        // }
      }
    }
</script>

<style scoped  lang="scss" >
  .codes {
    flex: 2;
    display: flex;

  .codeLeft {
    width:10%;

  ul {
    list-style: none;

  li {
    height: 30px;
    line-height: 30px;
  }

  .code-title {
    font-size: 16px;
    font-weight: 600;
  }

  .list {
    font-size: 14px;
  }
  }
  }
    .codeRight {
      flex:8;
    }
  }
.sec{
  display:flex;

}
</style>



