<template>
  <div class="codesss">
    <dl-header :active="titleActive">
    </dl-header>
    <div class="codes">
      <div class="codeLeft">
        <ul v-for="(i,index) in compon" :key="index">
          <li class="code-title" >{{i.name}}</li>
          <router-link v-for="(j,ind) in i.children" :key="ind" tag="li" :to="j.url"  class="list" >{{j.name}}</router-link>
        </ul>
      </div>
      <div class="codeRight">
        <router-view></router-view>
      </div>
    </div>
  </div>

</template>

<script>
  import dlHeader from "../../components/dl-header"

  export default {
    name: "codes",
    data() {
      return {
        titleActive: 3,
        compon: [
          {
            name:"开发指南",
            children:[ {
              url: "/codes/install",
              name: "安装"
            },{
              url: "/codes/use",
              name: "快速上手"
            },{
              url: "/codes/gongxian",
              name: "参与贡献"
            },{
              url: "/codes/updata",
              name: "更新日志"
            }
            ]
          },
          {
            name:"组件",
            children:[{
              url: "/codes/radio",
              name: "单选按钮"
            },
              {
                url: "/codes/select",
                name: "下拉菜单"
              },{
              url: "/codes/from",
              name: "表单"
            },
              {
                url: "/codes/nav",
                name: "导航条"
              },{
              url: "/codes/loginCheck",
              name: "登录验证"
            }, {
                url: "/codes/paging",
                name: "分页"
              },{
              url: "/codes/breadCurmbs",
              name: "面包屑"
            },
              {
                url: "/codes/accordion",
                name: "手风琴"
              },{
                url: "/codes/verificationCode",
                name: "验证码"
              },{
              url: "/codes/fold",
              name: "折叠"
            },
              {
                url: "/codes/Rate",
                name: "评分"
              },

            ]
          }

        ]
      }
    },
    components: {
      dlHeader
    },
    methods:{
    }
  }
</script>

<style lang="scss" scoped>
  .router-link-active{
    color: red;
  }
  .codesss {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 0  0 0 10%;

    .codes {
      flex: 1;
      display: flex;

      .codeLeft {
        flex: 2;

        ul {
          list-style: none;

          li {
            height: 30px;
            line-height: 35px;
            padding: 0 0 0 20%;
            cursor: pointer;
          }

          .code-title {
            font-size: 20px;
            font-weight: 600;
          }

          .list {
            font-size: 16px;
          }
        }
      }

      .codeRight {
        flex: 8;
        height: 100%;
        overflow-y: auto;
      }
    }
  }

</style>



