<template>
    <div>
      <dl-header :active="titleActive"></dl-header>
      <!-- team -->
      <div class="team">
        <div class="container">
          <h3 class="agileits-title">我们的团队</h3>
          <iframe class="aboutss" src="/static/about/index.html"></iframe>
        </div>
      </div>
      <!-- //team -->
      <dl-footer></dl-footer>
    </div>
</template>

<script>
  import dlHeader from "../../components/dl-header"
  import dlFooter from "../../components/dl-footer"
    export default {
        name: "about" ,
      data(){
        return{
          titleActive:1
        }
      },
      components: {
        dlHeader,
        dlFooter
      }
    }
</script>

<style lang="scss" scoped>
  .team{
    width: 100%;
    height: 100%;
  }
.aboutss{
  width: 100%;
  height: 800px;
}
</style>



