<template>
    <div class="progressTest">
        <div class="title">
            <h3>Accordion</h3>
            <p>常用的手风琴组件。</p>
        </div>
        <div class="usage">
            <h3 class="title">基础用法</h3>
            <p>手风琴组件的基础用法。</p>
            <div class="useProgress">
                <dl-accordion :content="content" :maskbg="maskbg"></dl-accordion>
            </div>
            <div class="content">
                <div class="description">你可以使用<span>content</span>属性来定义手风琴插件的文本内容,蒙版的文本内容,背景色，用<span>maskbg</span>属性来定义蒙版的透明度</div>
                <div class="code">
                    <p><span><</span>dl-fold :title="title" :span="span" :p="p"<span>></span><span><</span>/dl-fold<span>></span></p>
                    <p class="top"><span><</span>script<span>></span></p>
                    <p>&nbsp;&nbsp;export default {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;data() {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return {</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;content:[</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{"cn":"示例一","mask":"蒙版一","cbg":"orange"},</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{"cn":"示例三","mask":"蒙版三","cbg":"blue"},</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{"cn":"示例四","mask":"蒙版四","cbg":"yellow"},</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{"cn":"示例五","mask":"蒙版五","cbg":"gray"}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;],</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;maskbg:" rgba(0,0,0,.6)",</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;}</p>
                    <p>&nbsp;&nbsp;}</p>
                    <p><span><</span>/script<span>></span></p>
                </div>
            </div>
            <div class="show" @click="show">查看代码</div>
        </div>
        <div class="usage">
            <h3 class="title">Attributes</h3>
            <div class="attributesTable">
                <table>
                    <thead>
                    <tr>
                        <td>参数</td>
                        <td>说明</td>
                        <td>类型</td>
                        <td>可选值</td>
                        <td>默认值</td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>content</td>
                        <td>手风琴插件的文本内容,蒙版的文本内容,背景色</td>
                        <td>Array</td>
                        <td>—</td>
                        <td>—</td>
                    </tr>
                    <tr>
                        <td>maskbg</td>
                        <td>蒙版的透明度</td>
                        <td>String</td>
                        <td>—</td>
                        <td>rgba(0,0,0,.6)</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import $ from 'jquery'
    export default {
        name: "accordion",
        data(){
            return{
                content:[
                    {"cn":"示例一","mask":"蒙版一","cbg":"orange"},
                    {"cn":"示例二","mask":"蒙版二","cbg":"green"},
                    {"cn":"示例三","mask":"蒙版三","cbg":"blue"},
                    {"cn":"示例四","mask":"蒙版四","cbg":"yellow"},
                    {"cn":"示例五","mask":"蒙版五","cbg":"gray"}
                ],
                maskbg:" rgba(0,0,0,.6)"
            }
        },
        methods:{
            show(){
                $(".content").toggle();
            }
        }
    }
</script>

<style scoped>
    .bottom{
        margin-bottom: 50px;
    }
    .progressTest{
        margin-top: 50px;
    }
    .title>h3{
        font-size: 28px;
        font-weight: 400;
        color: #1f2f3d;
        margin-bottom: 15px;
    }
    .title>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .usage>h3{
        font-size: 22px;
        font-weight: 400;
        color: #1f2f3d;
        margin-top: 55px;
        margin-bottom: 15px;
    }
    .usage>p{
        font-size: 14px;
        color: #5e6d82;
        line-height: 20px;
        margin-bottom: 15px;
    }
    .useProgress{
        border: 1px solid #ebebeb;
        padding: 24px;
    }
    .content{
        background-color: #fafafa;
        padding: 10px;
        border: 1px solid #ebebeb;
        border-top: none;
        display: none;
    }
    .top{
        margin-top: 10px;
    }
    .description{
        border: 1px solid #ebebeb;
        background-color: #fff;
        line-height: 22px;
        padding: 20px;
        border-radius: 3px;
        font-size: 14px;
        color: #666;
    }
    .description>span{
        color: #5e6d82;
        background-color: #e6effb;
        margin: 0 4px;
        display: inline-block;
        padding: 1px 5px;
        font-size: 12px;
        border-radius: 3px;
        height: 18px;
        line-height: 18px;
    }
    .code{
        padding: 20px 10px 10px;
        color: #3182bd;
        font-size: 12px;
    }
    .code>p{
        line-height: 20px;
    }
    .show{
        border: 1px solid #ebebeb;
        border-top: none;
        font-size: 14px;
        line-height: 44px;
        text-align: center;
        color: #5e6d82;
    }
    table{
        width: 100%;
        font-size: 14px;
        margin-bottom: 30px;
    }
    table tr{
        border-bottom: 1px solid #ebebeb;
    }
    table td{
        padding: 15px;
        color: #606266;
    }
    thead td{
        color: #909399;
    }
</style>


