<template>
  <div class="radio">
    <div class="title">
      <h3>radio 单选框</h3>
      <p>在一组被选项中进行单选。</p>
    </div>
    <h5>由于选项默认可见，不宜过多，若选项过多，建议使用Select选择器</h5>
    <div class="radio-eg">
      <p >
        <dl-radio
                    :name="1"
                    :options="options"
                    :title="title"
                    @change="updataVal">
        </dl-radio>
      </p>
    </div>
      <div class="show-demo">
        <h5  @click="handleChangeTag">查看代码</h5>
        <div class="demo"  v-if="tag">
          <p>< <span>template</span> > </p>
            <p>&nbsp;&nbsp;<<span>dl-radio</span>></p>
          <p> <span class='nbsp'></span> :options="options"</p>
          <p> <span class='nbsp'></span>:title="title"</p>
          <p> <span class='nbsp'></span>@change="updataVal"</p>
          <p>&nbsp;&nbsp;<<span>/dl-radio</span>></p>
          <p><<span>/template</span>></p>
          <p><<span>script</span>></p>
          <p> &nbsp;&nbsp;import dlRadio from './components/dlRadio' </p>
          <p>&nbsp;&nbsp;export default {</p>
          <p> &nbsp;&nbsp;&nbsp;&nbsp;name: "Order",</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;components: {
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;optionBox
            &nbsp;&nbsp;&nbsp;&nbsp;},</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;data() {</p>
          <p>  &nbsp;&nbsp;&nbsp;&nbsp;<span class='nbsp'></span> return {</p>
          <p> &nbsp;&nbsp;&nbsp;&nbsp; <span class='nbsp'></span> options: ['男','女'],</p>
          <p> &nbsp;&nbsp;&nbsp;&nbsp; <span class='nbsp'></span>  title:"性别",</p>
          <p> &nbsp;&nbsp;&nbsp;&nbsp;}</p>
          <p>&nbsp;&nbsp; }</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp;,methods:{</p>
          <p>&nbsp;&nbsp;&nbsp;&nbsp; <span class='nbsp'></span>  updataVal (index) {</p>
          <p> &nbsp;&nbsp;&nbsp;&nbsp;<span class='nbsp'></span>   console.log(index)</p>
          <p>  <span class='nbsp'></span> }</p>
          <p>  &nbsp;&nbsp;}</p>
          <p> }</p>
          <p><<span>/script</span>></p>
        </div>
      </div>
    <h2>radio API</h2>
    <table class="attr">
      <tr>
        <td>参数</td>
        <td>说明</td>
        <td>类型</td>
        <td>可选值</td>
        <td>默认值</td>
      </tr>
      <tr>
        <td>options</td>
        <td>选择项</td>
        <td>Array</td>
        <td>--</td>
        <td>--</td>
      </tr>
      <tr>
        <td>title</td>
        <td>标题，显示在选项上方</td>
        <td>String</td>
        <td>--</td>
        <td>--</td>
      </tr>
      <tr>
        <td>name</td>
        <td>原生 name 属性(用来控制单选)</td>
        <td>Number/String</td>
        <td>--</td>
        <td>--</td>
      </tr>
    </table>
    <h2>radio Events</h2>
    <table  class="attr">
      <tr>
        <td>事件名称</td>
        <td>说明</td>
        <td>回调参数</td>
      </tr>
      <tr>
        <td>change</td>
        <td>绑定值变化时触发的事件</td>
        <td>目前的选中值</td>
      </tr>
    </table>
    <h2>radio Attributes</h2>
    <table class="attr">
      <tr>
        <td>参数</td>
        <td>说明</td>
        <td>类型</td>
        <td>可选值</td>
        <td>默认值</td>
      </tr>
      <tr>
        <td>options</td>
        <td>单选项的内容</td>
        <td>Array</td>
        <td>--</td>
        <td>['男','女']</td>
      </tr>
      <tr>
        <td>active</td>
        <td>选中项的颜色</td>
        <td>string</td>
        <td>--</td>
        <td>skyblue</td>
      </tr>
      <tr>
        <td>checked</td>
        <td>默认选中</td>
        <td>Boolean</td>
        <td>--</td>
        <td>false</td>
      </tr>
      <tr>
    </tr>
    </table>
  </div>
</template>

<script>
  import dlRadio from './components/dlRadio'
  export default {
    name: 'orderHome',
    components:  {
      dlRadio,
    },
    data () {
      return {
        options: ['男','女'],
        title:"性别",
        tag:false
      }
    },
    methods: {
      updataVal (index) {
        console.log(index)
      },
      handleChangeTag(){
        console.log(this.tag)
        this.tag = this.tag === false ? true : false
      }
    }
  }

</script>

<style lang='scss' scoped>
h1,h2,h5{
  font-weight: 400;
  color: #1f2f3d;
}
h2{
  /*border:1px solid #EBEBEB;*/
  width:20%;
  margin-bottom:50px;
}
.nbsp{
  display: inline-block;
  width:30px;
}
  .radio-eg{
    padding: 24px;
    height:88px;
    border:1px solid #EBEBEB;
    width:50%;
    margin-top: 10px;
  }
  .radio-eg p{
    z-index: 666;
  }
  .show-demo{
    border:1px solid #EBEBEB;
    width:49.45%;
    line-height:4px;
  }
.show-demo h5{
  text-align: center;
  cursor:pointer;
  border: 1px solid #ebebeb;
  border-top: none;
  font-size: 14px;
  line-height: 44px;
  text-align: center;
  color: #5e6d82;
}
  .demo{
    width:100%;
    padding:24px;
    border:1px solid #EBEBEB;
    background:#FAFAFA;
  }
  .demo p{
    color:#3182BD;
  }
  .attr{
    border-collapse:collapse;
    margin-bottom:80px;
  }
  .attr tr td{
    border-bottom: 1px solid #DCDFE6;
    padding:24px;
  }
.title>h3{
  font-size: 28px;
  font-weight: 400;
  color: #1f2f3d;
  margin-bottom: 15px;
}
.title>p{
  font-size: 14px;
  color: #5e6d82;
  line-height: 20px;
  margin-bottom: 15px;
}
</style>



