<template>
  <div class="selectlist">
    <div class="title">
      <h3>Select 选择器</h3>
      <p>当选项过多时，使用下拉菜单展示并选择内容。。</p>
    </div>
    <div class="select-eg">
      <p>
        <dl-select :selectWidth="selectWidth"
              :subject="subject"
              :selectColorbg="selectColorbg"
              :selectContent="selectContent"
              :selectHeight="selectHeight"
              :selectFont="selectFont"
              @changeSelect="datasss">
        </dl-select>
      </p>

    </div>
      <div class="show-demo">
        <h5  @click="handleChangeTag">查看代码</h5>
        <div class="demo"  v-if="tag">
          <p>< <span>template</span> > </p>
            <p><<span>down</span>></p>
          <p> <span class='nbsp'></span> :selectWidth="selectWidth"</p>
          <p> <span class='nbsp'></span>:subject="subject"</p>
          <p> <span class='nbsp'></span>:selectColor="selectColorbg"</p>
          <p> <span class='nbsp'></span>:selectContent="selectContent"</p>
          <p><span class='nbsp'></span>:selectHeight="selectHeight"</p>
          <p> <span class='nbsp'></span>:selectFont="selectFont"</p>
          <p> <span class='nbsp'></span>@changeSelect="datasss"</p>
          <p><<span>/down</span>></p>
          <p><<span>/template</span>></p>
          <p><<span>script</span>></p>
          <p> import down from '@/pages/select/down.vue' </p>
          <!--<p>export default {</p>-->
          <p>export default {</p>
          <p>components: {
            down
            },</p>
          <p>data() {</p>
          <p>  <span class='nbsp'></span> return {</p>
          <p>  <span class='nbsp'></span> selectWidth: {</p>
          <p>  <span class='nbsp'></span> default:200</p>
          <p> },</p>
          <p>  <span class='nbsp'></span> selectHeight: {</p>
          <p>   <span class='nbsp'></span> default:40</p>
          <p>  },</p>
          <p>  <span class='nbsp'></span> selectFont:{</p>
          <p>  <span class='nbsp'></span> default:13</p>
          <p>},</p>
          <p>  <span class='nbsp'></span> subject:['糖醋里脊','回锅肉','头角茄子','宫保鸡丁','酸辣土豆'],</p>
          <p>   selectColorbg:{</p>
          <p>  <span class='nbsp'></span>  default:'#fff'</p>
          <p>  },</p>
          <p>  selectContent:{</p>
          <p>  <span class='nbsp'></span> text: "请点菜"</p>
          <p> <span class='nbsp'></span>  }</p>
          <p>  }</p>
          <p> }</p>
          <p>,methods:{</p>
          <p> <span class='nbsp'></span>  datasss(value){</p>
          <p> <span class='nbsp'></span>  console.log(value)</p>
          <p>  <span class='nbsp'></span> }</p>
          <p> }</p>
          <p><<span>/script</span>></p>
        </div>
      </div>
    <h2>Select Attributes</h2>
    <table class="attr">
      <tr>
        <td>参数</td>
        <td>说明</td>
        <td>类型</td>
        <td>可选值</td>
        <td>默认值</td>
      </tr>
      <tr>
        <td>selectWidth</td>
        <td>输入宽的大小</td>
        <td>string</td>
        <td>--</td>
        <td>200</td>
      </tr>
      <tr>
        <td>selectHeight</td>
        <td>输入高的大小</td>
        <td>string</td>
        <td>--</td>
        <td>40</td>
      </tr>
      <tr>
        <td>selectContent</td>
        <td>下拉菜单文本框的标题</td>
        <td>string</td>
        <td>--</td>
        <td>请选择</td>
      </tr>
    </table>
    <h2>Select Events</h2>
    <table  class="attr">
      <tr>
        <td>事件名称</td>
        <td>说明</td>
        <td>回调参数</td>
      </tr>
      <tr>
        <td>changeSelect</td>
        <td>改变菜单文本框的标题内容是触发</td>
        <td>目前的选中值</td>
      </tr>
      <tr>
        <td>move</td>
        <td>模拟hover效果</td>
        <td>(event: Event)</td>
      </tr>
      <tr>
        <td>out</td>
        <td>模拟hover效果</td>
        <td>(event: Event)</td>
      </tr>
      <tr>
        <td>triggerOption</td>
        <td>控制option的展示和选中后的高亮效果</td>
        <td>--</td>
      </tr>
    </table>
    <h2>Option Attributes</h2>
    <table class="attr">
      <tr>
        <td>参数</td>
        <td>说明</td>
        <td>类型</td>
        <td>可选值</td>
        <td>默认值</td>
      </tr>
      <tr>
        <td>subject</td>
        <td>下拉菜单中所输入的内容</td>
        <td>string</td>
        <td>--</td>
        <td>1,2,3,4</td>
      </tr>
      <tr>
        <td>selectColorbg</td>
        <td>下拉菜单文本框的背景色</td>
        <td>string</td>
        <td>--</td>
        <td>#fff</td>
      </tr>
      <tr>
      <td>selectFont</td>
      <td>下拉菜单文本内容的字体大小</td>
      <td>number</td>
      <td>--</td>
      <td>12</td>
    </tr>
    </table>
  </div>
</template>

<script>
  import DlSelect from './components/DlSelect.vue'
export default {
  name: 'Home',
  components: {
    DlSelect
  },
data() {
  return {
    selectWidth: {
      default:200
    },
    selectHeight: {
      default:40
    },
    selectFont:{
      default:13
    },
    subject:['糖醋里脊','回锅肉','头角茄子','宫保鸡丁','酸辣土豆'],
    selectColorbg:{
          default:'#fff'
       },
    selectContent:{
      text: "请点菜"
       },
    tag:false
    }
    }
    ,methods:{
    datasss(value){
      console.log(value)
    },
    handleChangeTag(){
      console.log(this.tag)
      this.tag = this.tag === false ? true : false
    }
  }
  }


</script>

<style lang='scss' scoped>
h1,h2,h5{
  font-weight: 400;
  color: #1f2f3d;
}
h2{
  /*border:1px solid #EBEBEB;*/
  width:20%;
  margin-bottom:50px;
}
.nbsp{
  display: inline-block;
  width:30px;
}
  .select-eg{
    padding: 24px;
    height:88px;
    border:1px solid #EBEBEB;
    width:50%;
  }
  .show-demo{
    border:1px solid #EBEBEB;
    width:50%;
    left:8px;
    top:328px;
    /*text-align: center;*/
    line-height:4px;
  }
.show-demo h5{
  text-align: center;
  cursor:pointer;
  border: 1px solid #ebebeb;
  border-top: none;
  font-size: 14px;
  line-height: 44px;
  text-align: center;
  color: #5e6d82;
}
  .demo{
    width:100%;
    padding:24px;
    border:1px solid #EBEBEB;
    background:#FAFAFA;
  }
  .demo p{
    color:#3182BD;
  }
  .attr{
    border-collapse:collapse;
    margin-bottom:80px;
  }
  .attr tr td{
    border-bottom: 1px solid #DCDFE6;
    padding:24px;
  }
.title>h3{
  font-size: 28px;
  font-weight: 400;
  color: #1f2f3d;
  margin-bottom: 15px;
}
.title>p{
  font-size: 14px;
  color: #5e6d82;
  line-height: 20px;
  margin-bottom: 15px;
}
</style>



