<template>
  <div class="radioBox">
    <div>{{title}}</div>
    <label class="my-radio" v-for="(item, index) in options" :key="index" >
      <input class="radio-icon"checked type="radio" :name="name" :value="item"  @change="updataInp(index)">
      <span class="radio-content"  :class="active===index?'g':''" >{{item}}</span>
    </label>
  </div>
</template>

<script>
  export default {
    name: "dlRadio",
    model: { // 自定义 v-model的 prop和event
      prop: 'options', // 属性
      //event: 'updataInp' // 事件
    },
    data(){
      return{
        active:-1
      }
    },
    props: {
      options: { // radio的value属性，每一项的名称
        type: Array, // 数据类型
        required: true // 是否为必填的字符串
        // default: 100默认值（仅示意）
      },
      title:{//标题
        type:String,
        required: true
      },
      name: {
        type: [Number, String],
        required: true
      }
    },
    methods: {
      updataInp (index) {
        this.active = index;
        this.$emit('change', index)
        console.log(index)
      }
    }
  }
</script>

<style scoped>
  u.radio-icon:checked + .radio-content {
    border-radius: 11px;
    background-color: #ffe200;
    border-color: #ffe200;
  }
  .my-radio{
    margin-right:10px;
  }
 /* h3{
    font-size:50px;
    color: slateblue;
    margin-bottom: 15px;
  }*/
  .g{
    color: skyblue;
  }

</style>



