<template>
  <div class="pcdemo">
    <h3 class="agileits-title">PC端案例</h3>
    <h1>{{swiperlist[active].name}}</h1>
      <div class="swiper">
        <el-carousel :interval="5000" arrow="always" @change="changes">
          <el-carousel-item v-for="item in swiperlist" :key="item.id">
            <h3><img :src="item.imgUrl"/></h3>
          </el-carousel-item>
        </el-carousel>
      </div>
  </div>
</template>

<script>

  export default {
    name: 'Pc',
    data(){
      return{
        swiperlist:[
          {
            id:"1",
            name:"仿手礼网",
            imgUrl:"/static/images/pclqq.PNG"
          },
          {
            id:"2",
            name:"仿Vans",
            imgUrl:"/static/images/pcvans.jpg"
          },
          {
            id:"3",
            name:"仿同城帮",
            imgUrl:"/static/images/pxtcb.jpg"
          },
          {
            id:"4",
            name:"仿SK-II",
            imgUrl:"/static/images/pcskii.PNG"
          },
          {
            id:"5",
            name:"仿Boss直聘",
            imgUrl:"/static/images/pcboss.png"
          },
          {
            id:"6",
            name:"仿兰蔻",
            imgUrl:"/static/images/pcnk.png"
          }
        ],
        active:0
      }
    },
    methods:{
      changes(index){
        this.active=index;
        // console.log(index)
      }
    }
  }
</script>

<style>
  .pcdemo{
    margin-bottom:120px;
  }
  .swiper{
   width:90%;
    height:500px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    /*line-height: 300px;*/
    margin: 0;
    height:500px;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .el-carousel__container {
    position: relative;
    height: 500px;
  }
  img{
    width:100%;
    height:100%;
  }
  .agileits-title{
    margin-bottom:-20px;
  }

</style>












